import { TwitterService } from './service/twit.service';
import { DashboardComponent } from './dashboard/dashboard.component';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { AppComponent } from './app.component';
import { AppRoutes } from './routes';
import { SearchComponent } from './search/search.component';
import { HistoricalComponent } from './dashboard/historical/historical.component';
import { TwitterComponent } from './dashboard/twitter/twitter.component';
import { ChartModule } from 'angular2-highcharts';

declare var require: any;

@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    SearchComponent,
    HistoricalComponent,
    TwitterComponent
  ],
  imports: [
    BrowserModule,
    AppRoutes,
    HttpClientModule,
    ChartModule.forRoot(require('highcharts'))
  ],
  providers: [HttpClientModule, TwitterService],
  bootstrap: [AppComponent]
})
export class AppModule { }



