import { TwitterService } from './../../service/twit.service';
import { Component, OnInit } from '@angular/core';
import * as _ from 'lodash';

@Component({
  selector: 'historical',
  templateUrl: './historical.component.html',
  styleUrls: ['./historical.component.css']
})
export class HistoricalComponent implements OnInit {
  title = 'historical';
  stockData = [];
  moreTradedDays = [];
  highestProfit = [];
  calCulations = {};
  options: Object;
  loading = true;


  constructor(private appSerive: TwitterService) {
    //
  }

  public ngOnInit() {

    let getSearchQuery = localStorage.getItem('searchValue');

    if (getSearchQuery === 'hdfcbank') {
      getSearchQuery = 'hdfc';
    } else {
      getSearchQuery = 'hdfc';
    }
    this.appSerive.getHistoricalData(getSearchQuery).subscribe(result => {
      this.loading = false;
      this.stockData = result;
      this.stockData.forEach(data => {
        let d = new Date(data['Date']);
        data['dayName'] = d.toString().split(' ')[0];
        data['dayProfit'] = data['Last Price'] - data['Open Price'];
      });
      console.log(this.stockData);
      this.moreTradedDays = _.orderBy(this.stockData, ['Total Traded Quantity'], ['desc']);
      this.highestProfit = _.orderBy(this.stockData, ['dayProfit'], ['desc']);
      this.calCulations['profitEarned'] = 0;
      this.calCulations['lossOccured'] = 0;
      this.calCulations['mondayProfit'] = 0;
      this.calCulations['tuesdayProfit'] = 0;
      this.calCulations['wednesdayProfit'] = 0;
      this.calCulations['thursdayProfit'] = 0;
      this.calCulations['fridayProfit'] = 0;
      this.calCulations['mondayLoss'] = 0;
      this.calCulations['tuesdayLoss'] = 0;
      this.calCulations['wednesdayLoss'] = 0;
      this.calCulations['thursdayLoss'] = 0;
      this.calCulations['fridayLoss'] = 0;
      this.highestProfit.forEach(data => {
        if (data['dayProfit'] >= 0) {
          this.calCulations['profitEarned']++;
          switch (data['dayName']) {
            case 'Mon': this.calCulations['mondayProfit']++;
              break;
            case 'Tue': this.calCulations['tuesdayProfit']++;
              break;
            case 'Wed': this.calCulations['wednesdayProfit']++;
              break;
            case 'Thu': this.calCulations['thursdayProfit']++;
              break;
            case 'Fri': this.calCulations['fridayProfit']++;
              break;
          }
        } else {
          this.calCulations['lossOccured']++;
          switch (data['dayName']) {
            case 'Mon': this.calCulations['mondayLoss']++;
              break;
            case 'Tue': this.calCulations['tuesdayLoss']++;
              break;
            case 'Wed': this.calCulations['wednesdayLoss']++;
              break;
            case 'Thu': this.calCulations['thursdayLoss']++;
              break;
            case 'Fri': this.calCulations['fridayLoss']++;
              break;
          }
        }
      });

      this.getChartData();
    }



  );
  }

  public getChartData() {
    console.log(this.calCulations['mondayProfit'] * 100 / this.highestProfit.length);
    this.options = {
      chart: {
        type: 'column'
    },
    title: {
        text: 'Average market share by day wise'
    },
    xAxis: {
        type: 'category'
    },
    yAxis: {
        title: {
            text: 'Profit earned on day wise'
        }

    },
    legend: {
        enabled: false
    },
    plotOptions: {
        series: {
            borderWidth: 0,
            dataLabels: {
                enabled: true,
                format: '{point.y:.1f}%'
            }
        }
    },

    tooltip: {
        headerFormat: '<span style="font-size:11px">{series.name}</span><br>',
        pointFormat: '<span style="color:{point.color}">{point.name}</span>: <b>{point.y:.2f}%</b> of total<br/>'
    },

    series: [{
        name: 'Brands',
        colorByPoint: true,
        data: [{
            name: 'Monday',
            y: this.calCulations['mondayProfit'] * 100 / this.highestProfit.length,
            drilldown: 'Microsoft Internet Explorer'
        }, {
            name: 'Tuesday',
            y: this.calCulations['tuesdayProfit'] * 100 / this.highestProfit.length,
            drilldown: 'Chrome'
        }, {
            name: 'Wednesday',
            y: this.calCulations['wednesdayProfit'] * 100 / this.highestProfit.length,
            drilldown: 'Firefox'
        }, {
            name: 'Thursday',
            y: this.calCulations['thursdayProfit'] * 100 / this.highestProfit.length,
            drilldown: 'Safari'
        }, {
            name: 'Friday',
            y: this.calCulations['fridayProfit'] * 100 / this.highestProfit.length,
            drilldown: 'Opera'
        }]
    }]
  };
  }
}



