import { Injectable, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/delay';
import 'rxjs/add/operator/map';

@Injectable()
export class TwitterService implements OnInit {
  constructor(protected http: HttpClient) {
    //
  }
  ngOnInit() {
      //
  }

  public searchStock(args): Observable<any> {
    const url = 'https://darkknightwebapi.azurewebsites.net/api/values?text=%23' + args;
    return this.http.get(url)
      .map((response: Response) => {
        return response;
      }
      );
  }

  public getHistoricalData(company: string): Observable<any> {
    // get users from api
    return this.http.get('assets/data/' + company + '.json')
        .map((response: Response) => {
            return response;
        }
      );
}

  public getApiHeaders(method?: string, token?: string): HttpHeaders {
    let retVal = new HttpHeaders({
      'jsonformat': 'compact'
    });
    if (method && method !== 'get') {
      retVal = retVal.append('Content-Type', 'application/json');
    }
    if (token) {
      retVal = retVal.append('Authorization', 'Bearer ' + token);
    }
    return retVal;
  }

  public handleError(error: any) {
    console.log(error);
  }
}



