
import { TwitterService } from './../../service/twit.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
declare var require: any;
import { ChartModule } from 'angular2-highcharts';
const ChartModuleMore = require('highcharts/highcharts-more');
const HCSoldGauge = require('highcharts/modules/solid-gauge');
import * as Highcharts from 'highcharts';
ChartModuleMore(Highcharts);
HCSoldGauge(Highcharts);

@Component({
  selector: 'twitter',
  templateUrl: './twitter.component.html',
  styleUrls: ['./twitter.component.css']
})
export class TwitterComponent implements OnInit {
  title = 'historical';
  options: Object;
  guageChart: Object;
  serachquery = '';
  twitterResult;
  loading = true;
  constructor(private twitterService: TwitterService,	private route: ActivatedRoute) {
    //
  }

  public ngOnInit() {

    this.serachquery = this.route.snapshot.queryParams['search'];
    if (!this.serachquery) {
      this.serachquery = localStorage.getItem('searchValue');
    }
    if (!this.twitterResult) {
      this.loading = true;
      this.twitterService.searchStock(this.serachquery).subscribe(result => {
        this.loading = false;
        console.log(result);
        this.getChartData(result);
        this.twitterResult = result;
        this.getGuageData(result);
      });
    } else {
      this.loading = false;
      this.getChartData(this.twitterResult);
      this.getGuageData(this.twitterResult);
    }
  }

  public getChartData(result: any[]) {
    let resdata = [];
    let resultTime = [];
    result.forEach(res => {
      resdata.push(res['Score'].toFixed(4) * 100);
      resultTime.push(res['TweetDate'].split(' ')[3].split(':')[0]);
    });
    console.log(resdata);
    this.options = {
      chart: {
        type: 'line'
    },
    title: {
        text: 'Twitter sentiment data for ' + this.serachquery
    },
    subtitle: {
        text: 'Source : Dark Knight'
    },
    xAxis: {
        categories: resultTime
    },
    yAxis: {
        title: {
            text: 'Sentimental (%)'
        }
    },
    plotOptions: {
        line: {
            dataLabels: {
                enabled: true
            },
            enableMouseTracking: false
        }
    },
    series: [{
        name:  this.serachquery,
        data: resdata
    }]
  };

}

  public getGuageData(result: any[]) {

    let resdata = [];
    let resultTime = [];
    result.forEach(res => {
      resdata.push(res['Score'].toFixed(4) * 100);
    });
    let sum = 0;
    resdata.forEach(r => {
        sum = sum + r;
    });
    let indicator = sum / resdata.length;
    console.log(sum / resdata.length);


    this.guageChart = {

    chart: {
      type: 'gauge',
      plotBackgroundColor: null,
      plotBackgroundImage: null,
      plotBorderWidth: 0,
      plotShadow: false
  },

  title: {
      text: 'Sentiment Oscillators'
  },

  pane: {
      startAngle: -150,
      endAngle: 150,
      background: [{
          backgroundColor: {
              linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
              stops: [
                  [0, '#FFF'],
                  [1, '#333']
              ]
          },
          borderWidth: 0,
          outerRadius: '109%'
      }, {
          backgroundColor: {
              linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
              stops: [
                  [0, '#333'],
                  [1, '#FFF']
              ]
          },
          borderWidth: 1,
          outerRadius: '107%'
      }, {
          // default background
      }, {
          backgroundColor: '#DDD',
          borderWidth: 0,
          outerRadius: '105%',
          innerRadius: '103%'
      }]
  },

  // the value axis
  yAxis: {
      min: 0,
      max: 100,

      minorTickInterval: 'auto',
      minorTickWidth: 1,
      minorTickLength: 10,
      minorTickPosition: 'inside',
      minorTickColor: '#666',

      tickPixelInterval: 30,
      tickWidth: 2,
      tickPosition: 'inside',
      tickLength: 10,
      tickColor: '#666',
      labels: {
          step: 2,
          rotation: 'auto'
      },
      title: {
          text: 'Recommandation'
      },
      plotBands: [{
          from: 0,
          to: 20,
          text:'strong',
          color: '#8d0000' // green
      }, {
          from: 20,
          to: 40,
          color: '#e57878' // yellow
      },
      {
        from: 40,
        to: 60,
        color: '#99f6ff' // red
    }, {
          from: 60,
          to: 80,
          color: '#2cfc7f' // red
      },
       {
          from: 80,
          to: 100,
          color: '#00762f' // red
      }]
  },

  series: [{
      name: 'Speed',
      data: [indicator],
      tooltip: {
          valueSuffix: 'recommandation'
      }
  }]

},
      // Add some life
      function (chart) {
        if (!chart.renderer.forExport) {
          setInterval(function () {
            //

          }, 5);
        }
      };
  }

}



